'use server';

/**
 * @fileOverview This file defines a Genkit flow for parsing resumes and extracting relevant information.
 *
 * It includes:
 * - `parseResume`: A function to parse resume data.
 * - `ResumeParsingInput`: The input type for the `parseResume` function, representing the resume content as a data URI.
 * - `ResumeParsingOutput`: The output type for the `parseResume` function, representing the extracted information.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const ResumeParsingInputSchema = z.object({
  resumeDataUri: z
    .string()
    .describe(
      'The resume file as a data URI that must include a MIME type and use Base64 encoding. Expected format: \'data:<mimetype>;base64,<encoded_data>\'.' // prettier-ignore
    ),
});
export type ResumeParsingInput = z.infer<typeof ResumeParsingInputSchema>;

const ResumeParsingOutputSchema = z.object({
  skills: z
    .array(z.string())
    .describe('A list of skills extracted from the resume.'),
  experienceSummary: z
    .string()
    .describe('A summary of the work experience listed in the resume.'),
  educationSummary: z
    .string()
    .describe('A summary of the education history in the resume.'),
});
export type ResumeParsingOutput = z.infer<typeof ResumeParsingOutputSchema>;

export async function parseResume(input: ResumeParsingInput): Promise<ResumeParsingOutput> {
  return parseResumeFlow(input);
}

const parseResumePrompt = ai.definePrompt({
  name: 'parseResumePrompt',
  input: {schema: ResumeParsingInputSchema},
  output: {schema: ResumeParsingOutputSchema},
  prompt: `You are an AI resume parser. Your job is to extract information from resumes.

  Analyze the resume provided and extract the following information:
  - A list of technical and soft skills.
  - A summary of the work experience.
  - A summary of the education history.

  Here is the resume data: {{media url=resumeDataUri}}

  Return the information in the following JSON format:
  {{json ResumeParsingOutputSchema}}`,
});

const parseResumeFlow = ai.defineFlow(
  {
    name: 'parseResumeFlow',
    inputSchema: ResumeParsingInputSchema,
    outputSchema: ResumeParsingOutputSchema,
  },
  async input => {
    const {output} = await parseResumePrompt(input);
    return output!;
  }
);
